import React, { useState,useRef } from 'react';
import DatePicker, { registerLocale } from 'react-datepicker';
import "./Modal.css";
import moment from 'moment';
import "react-datepicker/dist/react-datepicker.css";
import ko from "date-fns/locale/ko"

const ModalBasic = ({ setModalOpen, onCreate, eventList }) => {
    console.log(eventList)
    const [startDate, setStartDate] = useState(new Date());
    const time = moment(startDate).format('YYYY-MM-DD');
    const today = moment(new Date()).format('YYYY-MM-DD');
    console.log(time)
    registerLocale("ko",ko)

    //   
    const closeModal = () => {
        setModalOpen(false);
    };

    const [state, setState] = useState({
        title : "",
        date : time,
    })

    const handleChangeState = (e) => {
        setState({
            ...state,
            [e.target.name] : e.target.value,
        })
    }

    const handleSubmit = () => {
        onCreate(state.title, state.date);
        alert(' ');
        setState({
            title: "",
            date: today
        });
    }
    const StartSche = () => {
        return (
          <DatePicker
            selected={startDate}
            onChange={(date) => {
                setStartDate(date)
                const dates = moment(date).format('YYYY-MM-DD')
                setState({
                    ...state,
                    date : dates
                })
            }} 
            selectsStart
            startDate={startDate}
            isClearable
            locale={ko}
            dateFormat="yyyy MM dd"
            />
        );
      };

    return(
        <div>
            <div className='modal_bg'></div>
            <div className="container">
                <h2 className="text"> </h2>
                <div className="schedule">
                    
                    <input name="title" value={state.title} onChange={handleChangeState}></input>
                </div>
                <div className="date">
                    <StartSche />
                </div>
                <button className="close" onClick={closeModal}>X</button>
                <button className="addSche" onClick={handleSubmit}> </button>
            </div>
        </div>
    )
}

export default ModalBasic;
