import { useRef, useState } from "react";

const NoteItem = ({onEdit, onRemove, todo, id}) => {
    const [isEdit, setIsEdit] = useState(false);
    const toggleIsEdit = () => setIsEdit(!isEdit);
    const[localTodo, setLocalTodo] = useState(todo);
    const localTodoInput = useRef();

    const handleRemove = () => {
        onRemove(id);
        // if(window.confirm(`${id} todo  ?`)){
        //     onRemove(id);
        // }
    };

    const handleQuitEdit = () => {
        setIsEdit(false);
        setLocalTodo(todo);
    }

    const hanldeEdit = () => {
        if(localTodo.length < 2 ){
            localTodoInput.current.focus();
            return;
        }
        onEdit(id, localTodo);
            toggleIsEdit();
        // if(window.confirm(`${id} todo ?`)){
        //     onEdit(id, localTodo);
        //     toggleIsEdit();
        // }
    };

    return(
        <div className="NoteEditor">
            <div className="Todo_list">
                {isEdit ? (<><input ref={localTodoInput} value={localTodo} onChange={(e)=>setLocalTodo(e.target.value)} /></>) : (<>{todo}</>)}
            </div>
            {isEdit ? (<>
            <button onClick={handleQuitEdit}></button>
            <button onClick={hanldeEdit}></button></>) :
            (<>
            <button onClick={handleRemove}></button>
            <button onClick={toggleIsEdit}></button>
            </>)}
            
        </div>
    );
}

export default NoteItem;